import { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/black-and-white.css";
// import required modules
import { Navigation } from "swiper";
import http from "../api";

import { useEffect } from "react";
import ContentLoader from "react-content-loader";
import { useHistory } from "react-router-dom";

//sample data
const _data = [
  { imageName: require("../assets/images/Banners/banner7.jpg") },
  { imageName: require("../assets/images/Banners/banner5.jpg") },
  { imageName: require("../assets/images/Banners/banner1.jpg") },
  { imageName: require("../assets/images/Banners/banner2.jpg") },
  { imageName: require("../assets/images/Banners/banner3.jpg") },
  { imageName: require("../assets/images/Banners/banner4.jpg") },
  { imageName: require("../assets/images/Banners/banner6.jpg") },
  { imageName: require("../assets/images/Banners/banner8.jpg") },
];
export default function ShopCarousel({ categoryId = 2, title }) {
  const api = http();
  const history = useHistory();
  const [data, setData] = useState();
  const [loaded, setLoaded] = useState(false);
  useEffect(() => {
    api.GetCategories(
      ({ data }) => {
        setData(data);
        setLoaded(true);
      },
      {
        defType: 2,
        page: 1,
        sortValue: 0,
        categoryId,
      }
    );
  }, []);

  if (!loaded) {
    return (
      <div className="row mt-lg-5 mt-2 justify-content-center mb-4">
        <ContentLoader
          backgroundColor={"#fff"}
          foregroundColor={"#e4e4e4e4"}
          viewBox="0 0 380 70"
        >
          {/* Only SVG shapes */}
          <rect x="0" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="70" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="150" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="230" y="0" rx="5" ry="5" width="60" height="70" />
          <rect x="310" y="0" rx="5" ry="5" width="60" height="70" />
        </ContentLoader>
      </div>
    );
  }
  return (
    <div className="col-lg-12 mt-3 w-100">
      <div className="d-flex flex-row align-items-center justify-content-between">
        <span className="title-text" style={{ fontSize: 17 }}>
          فروشگاه ماشین تو
        </span>
        <div className="dash" />
        <p
          className="note-text tx-link"
          style={{ cursor: "pointer" }}
          onClick={() => history.push(`/shop`)}
        >
          مشاهده همه
        </p>
      </div>

      <Swiper
        // slidesPerView={3}

        modules={[Navigation]}
        // navigation={true}
        breakpoints={{
          320: {
            slidesPerView: 1.2,
            spaceBetween: 10,
          },
          // when window width is >= 480px
          480: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          // when window width is >= 640px
          640: {
            slidesPerView: 2,
            spaceBetween: 25,
          },
          720: {
            slidesPerView: 3,
            spaceBetween: 25,
          },
          960: {
            slidesPerView: 4,
            spaceBetween: 25,
          },
          1200: {
            slidesPerView: 5,
            spaceBetween: 25,
          },
        }}
        className="new-car-slider-container "
      >
        {data.map((item, key) => (
          <SwiperSlide
            className="nbt shadow"
            style={{ overflow: "hidden", borderRadius: 15 }}
            key={key}
            onClick={() => history.push(`/shoplist?categoryId=${item.id}`)}
          >
            <LazyLoadImage
              height={180}
              width={"100%"}
              src={`${
                process.env.NODE_ENV == "development"
                  ? "https://localhost:7045"
                  : "https://mashinto.com"
              }/media/ShopCategory/${item.bannerImage}`}
              alt={"item.title"}
              style={{ maxHeight: 180, width: "100%" }}
              onError={({ currentTarget }) => {
                currentTarget.onerror = null; // prevents looping
                currentTarget.src =
                  "https://mashinto.com/media/ads/defualt.png";
                currentTarget.style = "max-height:180px";
              }}
            />
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}
